import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddSubTypeOfHousingComponent } from './add-sub-type-of-housing.component';

const routes: Routes = [
  {
    path: '',
    component: AddSubTypeOfHousingComponent,
    data: {
      title: 'Add Sub Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SubTypeOfHousingCrudRoutingModule {}



