import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi ,CityApi,Sub_type_of_housing_masterApi,Type_of_housing_masterApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
import { promise } from 'protractor';
import { encode } from 'punycode';
import { utf8Encode } from '@angular/compiler/src/util';


declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-sub-type-of-housing.component.html'
})
export class AddSubTypeOfHousingComponent {
	@ViewChild('typeOfHousingName') name: NgModel;
	display: boolean = false; result: any;
	submitted: boolean = false;
	typeOfHousingName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	data: any;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	housingNameData = [];
	cityName:0;
	subTypOfHusngName:string;
	subTypeOfHousngStatus= false;

	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private cityApi:CityApi,  private subTypHousngApi:Sub_type_of_housing_masterApi,private type_of_housing_api:Type_of_housing_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		
		this.rolename = this.userDetails.userDet.roles;
	}


	async submit(form) {
		this.submitted = true;
		let validFlag = true;
		if (form.valid) {

			//function call to change base64 format data to Image
			var dataObj = {
				"name": this.capital_letter(this.subTypOfHusngName),
				"parent_id": this.typeOfHousingName,
				"status": false
			}
				
				this.loading = true;
				let totalCount;
				this.ifNameExist(dataObj).then((count)=>{
					totalCount = count;
					console.log(totalCount);
					if(totalCount > 0){
						this.loading = false;
						this.display=true;
						this.result = 'Sub type of housing name already exist.';
					}else{
						this.subTypHousngApi.upsert(dataObj).subscribe((data)=>{
							this.loading = false;
							this.display=true;
									this.result = 'success';
						},(err)=>{
							this.loading = false;
							this.display=true;
									this.result = 'Something went wrong.';
						});
					}
				},(err)=>{
					this.loading = false;
							this.display=true;
									this.result = 'Something went wrong.';
				});
				
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{	

				this.subTypHousngApi.getSubTypOfHousngCountName({"name": dataObj.name,"parent_id":dataObj.parent_id}).subscribe((data)=>{
					console.log("count",data);
					resolve(data.result[0].total);
				},err=>{
					reject(err);
				});
			}catch(err){
				reject(err);
			}
		});
		
		
	}

	ngOnInit(): void {
		this.getTypeOfHousing();
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["sub-type-of-housing"]);
	}

	getTypeOfHousing(){
		
		return new Promise((resolve,reject)=>{
			try{

				this.type_of_housing_api.find().subscribe(
					(data:any)=>{
						
						this.housingNameData = [];
						for (const key in data) {
							if (data[key].id != 1 && data[key].id != 4) {
								this.housingNameData.push(data[key]);
								
							}
						}
						
						resolve(this.housingNameData);
					},
					err=>{
						this.loading = false;
						this.display=false;
							this.result = 'Something went wrong.';
						reject(true);
					}
				);
			}catch(err){
				this.loading = false;
				this.display=false;
							this.result = 'Something went wrong.';
				reject(true);
			}

		});
	}

	
}


